import React from 'react';
import s from '@/styles/app.module.scss';
import { useAppDispatch, useTypedSelector } from "@/hooks/store.hooks";
import { changeName } from "@/store/Slice/profile.slice";
const App = () => {
    const dispatch = useAppDispatch();
    const { user } = useTypedSelector(state => state.profile);
    return (React.createElement("div", { className: s.app },
        React.createElement("h1", null, user.name),
        React.createElement("input", { type: "text", onChange: (e) => dispatch(changeName(e.target.value)) })));
};
export default App;
